import { useEffect, useRef } from "react"


export const useUpdate = (fn:()=>void , deps: any[]) => {
    let count = useRef(0)
    // renderrendercount.current+1.
    useEffect(() => {
        count.current += 1
    }, [])
    // tags
    useEffect(() => {
        if (count .current > 1) {
            console.log('set item')
            fn()
        }
    }, deps)
}
