import Icon from "components/Icon"
import { Input } from "components/Input"
import Layout from "components/Layout"
import Nav from "components/Nav"
import { Fragment } from "react"
import { useParams } from "react-router"
import styled from "styled-components"
import { useTags } from "useTags"
import { Button } from "./Tags/Button"
import { Center } from "./Tags/Center"
import { Space } from "./Tags/Space"

type Params = {
    tagId: string
}

const TopBar = styled.header`
    display:flex;
    justify-content: space-between;
    align-items: center;
    line-height: 20px;
    padding: 14px;
    background: white;
    border-bottom: 1px solid #000;
`
const InputWrapper = styled.div`
    background: #cccff3;
    padding: 16px 10px;
    margin-top: 2px;
    > label > span {
        padding: 0 10px;
    }
`

const Tag: React.FC = () => {
    // tags  {id: number, name: string}
    const { findTag, updateTag,deleteTag } = useTags()
    // 
    const { tagId } = useParams<Params>()
    // 
    const tag = findTag(parseInt(tagId))
    // 
    const onClickBack = ()=>{
        window.history.back()
    } 

    return (
        <Fragment>
            <Layout>
                <TopBar>
                    <Icon name="right" onClick={onClickBack}></Icon>
                    <span></span>
                    <Icon></Icon>
                </TopBar>
                <InputWrapper>
                    <Input type="text" label="" value={tag.name} onChange={(e) => {
                        // tag.name 
                        console.log(e.target.value)
                        updateTag(tag.id, { name: e.target.value })
                    }} />
                </InputWrapper>
                <Space />
                <Space />
                <Space />
                <Center>
                    <Button onClick={()=>{ deleteTag(tag.id) }}></Button>
                </Center>
            </Layout>
            <Nav />
        </Fragment>
    )
}

export { Tag }